import {  NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {path:'' , redirectTo:'home',pathMatch:'full'},
  {path:'home',loadChildren:()=>import('./home/home.module').then(m=>m.HomeModule)},
  {path:'tasks',loadChildren:()=>import('./tasks/tasks.module').then(m=>m.TaskModule)},
  {path:'login',loadChildren:()=>import('./login/login.module').then(m=>m.LoginModule)},
  {path:'note', loadChildren:()=>import('./note/note.module').then(m=>m.NoteModule)},
  {path:'userPage',loadChildren:()=>import('./user-page/uerPage.module').then(m=>m.UserPageModule)},
  {path:'weatherPage',loadChildren:()=>import('./weather-app/weatherpage.module').then(m=>m.WeatherModule)},
  {path:'userTask',loadChildren:()=>import('./user-tasks/user-tasks.module').then(m=>m.UserTasksModule)}



];


@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule {
 }
