import { R } from "@angular/cdk/keycodes";
import { Action } from "@ngrx/store";
import { UserTaskList } from "src/app/user/userTaskList.module";
import { TasksModule } from "../tasks-model/tasks-model.module";
import * as Tasks from './Task.Action';

export  interface State{
  tasks:TasksModule[],
  users:UserTaskList[],
  updatingTask: TasksModule,
  updatingIndex:number,
}
 

  const initializing:State = {
    tasks:[ new TasksModule('business','first','first description',new Date(),new Date(),'yes',false,[],'ToDo')
  
  ],
  users:[],
  updatingTask:null,
  updatingIndex:-1,
  }
export  function TaskReducers(
    //initializing the value
    state:State=initializing ,
    action:Tasks.TasksType
    ){
switch(action.type){

    case Tasks.Add_Task:
      return  {
            ...state,
            //to select it at TaskList
            tasks:[ ...state.tasks, action.payload
                 
                ]  


        }
    case Tasks.Add_Tasks :
      console.log(action.payload)
    return{
      ...state,
      tasks:[...action.payload]

    }


      case Tasks.Updated_Item:
   const Task = state.updatingTask;
   let updatingTask = {
     ...Task,
     ...action.payload
   }
  
   const updatingTasks = [...state.tasks];
   updatingTasks[state.updatingIndex]=updatingTask;

   return{
     ...state,
     tasks:updatingTasks,
     updatingTask:updatingTask,
     updatingIndex:-1,

     
   }
   case Tasks.ADD_INNER:

    
     
     
    
     const TaskUSer = state.updatingTask;
   
    return{
        ...state,
    
        users:[
          ...state.users,
           action.payload
          
          ]
           }
    case Tasks.Deleted_Item:
     
      return {...state,
        tasks: state.tasks.filter((ig,igIndex)=>{
         return  igIndex !== action.payload
        }),
        
 


      }

      case Tasks.StartEdit:
        return{
          ...state,
          updatingIndex: action.payload,
          updatingTask:{...state.tasks[action.payload]}

        }


      case Tasks.stopEdit:
        return {
          ...state,
          updatingIndex:-1,
        updatingTask:null 



        }
      
case Tasks.TITLEEDIT:
  const TitleTaskB = state.updatingTask;
  const UTaskTitle = {
    ...TitleTaskB,
    projectTitle:action.payload
  }
  const UpdatingTasksTitle = [...state.tasks] ;

  UpdatingTasksTitle[state.updatingIndex] = UTaskTitle;
        
return{
  ...state,
  tasks:UpdatingTasksTitle,
  updatingTask:UTaskTitle,
  updatingIndex:-1
}

case Tasks.TASKTYPEDIT:
  let TASKTYPEDIT = state.updatingTask

 const updateTaskType = {
   ...TASKTYPEDIT,
   taskType:action.payload
 };
 let updatingATasksType = [...state.tasks];
 updatingATasksType[state.updatingIndex]=updateTaskType

return {
  ...state,
  tasks:updatingATasksType,
  updatingTask:updateTaskType,
  updatingIndex:-1,

  
}
case Tasks.TAGS:
  let Tags = state.updatingTask

 const updateTaskTag = {
  ...Tags,
 tags:[
   ...Tags.tags,
   action.payload]
 };
 let updatingATasksTags = [...state.tasks];
 updatingATasksTags[state.updatingIndex]=updateTaskTag
return {
  ...state,
  tasks:updatingATasksTags,
  updatingTask:updateTaskTag,
  updatingIndex:-1,

  
}
case Tasks.DELETETAG:

const updatingTaskTag = state.updatingTask;
// updatingTaskTag.tags.splice(acti on.payload,1);



console.log(updatingTaskTag)
  return{...state    // updatingTask:updatingTask.tags.splice(action.payload,1)
  }


case Tasks.SCEdit:
  let SCEdit = state.updatingTask

 const updateTaskDate = {
  ...SCEdit,
  scheduleStart:action.payload.start,
  scheduleEnd:action.payload.end
 };
 let updatingATasksDate = [...state.tasks];
 updatingATasksDate[state.updatingIndex]=updateTaskDate
console.log(updateTaskDate)
return {
  ...state,
  tasks:updatingATasksDate,
  updatingTask:updateTaskDate,
  updatingIndex:-1,

  
}


case Tasks.ActiveProject:
  let task = state.updatingTask

 const updateTask = {
   ...task,
   statues:action.payload
 };
 let updatingATasks = [...state.tasks];
 updatingATasks[state.updatingIndex]=updateTask

return {
  ...state,
  tasks:updatingATasks,
  updatingTask:updateTask,
  updatingIndex:-1,

  
}
case Tasks.PROGRESS_VALUE:
  const TASKWITHPROGRESS = state.updatingTask
  const updateTaskProgress = {
    ...TASKWITHPROGRESS,
    progress:action.payload
  }
  const ALLTASKS =[...state.tasks]
  ALLTASKS[state.updatingIndex] = updateTaskProgress,
  console.log(ALLTASKS)
return {
  ...state,
  tasks: ALLTASKS,
  updatingTask:updateTaskProgress,
  updatingIndex:-1

}
case Tasks.DESCRIPTIONEDIT:
  const DESCRIPTIONEDIT = state.updatingTask
  const updateTaskDESCRIPTIONEDIT = {
    ...DESCRIPTIONEDIT,
    description:action.payload
  }
  const ALLTASKSDESCRIPTION =[...state.tasks]
  ALLTASKSDESCRIPTION[state.updatingIndex] = updateTaskDESCRIPTIONEDIT;
return {
  ...state,
  tasks: ALLTASKSDESCRIPTION,
  updatingTask:updateTaskDESCRIPTIONEDIT,
  updatingIndex:-1

}





case Tasks.PIORTYEDIT:
  const PIORTYEDIT = state.updatingTask
  const updateTaskPIORTYEDIT = {
    ...PIORTYEDIT,
    piorty:action.payload
  }
  const ALLTASKSPiorty =[...state.tasks]
  ALLTASKSPiorty[state.updatingIndex] = updateTaskPIORTYEDIT;
return {
  ...state,
  tasks: ALLTASKSPiorty,
  updatingTask:updateTaskPIORTYEDIT,
  updatingIndex:-1

}

case Tasks.setGroupOwner:
  const TASKWITHOUTGA = state.updatingTask;
  const updatingTASKWITHGO= {
    ...TASKWITHOUTGA,
    group:action.payload.group,
    owner:action.payload.owner
  };
  console.log(updatingTASKWITHGO)
  const UPDATINGATASKSWITHOG = [...state.tasks];
  UPDATINGATASKSWITHOG[state.updatingIndex] = updatingTASKWITHGO;
  console.log(UPDATINGATASKSWITHOG)
  return {
    ...state, 
    tasks:UPDATINGATASKSWITHOG,
    updatingTask:updatingTASKWITHGO,
    updatingIndex:-1
  }


default:return state;
      }



}