import { createReducer } from "@ngrx/store";
import { User } from "src/app/user/user.module";
import * as AuthActions from'./auth.actions'
export interface State{
    user:User;
    ErrorMessage:string
    loading:boolean
}

const initializeState:State = {
    user:null,
    ErrorMessage:null,
    loading:false

}
export const reducer = createReducer(
    initializeState,
    // (AuthActions.)
   
  );

export function authReducer(state:State=initializeState,action:AuthActions.AuthType){


    switch(action.type){
        case AuthActions.AuthenticatedSucces:
            const user = new User(
                action.payload.userId
                ,action.payload.email
                ,action.payload.token
                ,action.payload.expirationDate)
                console.log(action.payload)
            return{
                ...state,
                user:user,
                loading:false
                
            }
        case AuthActions.SIGNUP:
            const signUpUser = new User(
                action.payload.userId
                ,action.payload.email
                ,action.payload.token
                ,action.payload.expirationDate);
                console.log(action.payload)
            return {
                ...state,
                user:signUpUser,
                loading:false,
                ErrorMessage:null,

            }
        case AuthActions.LOGOUT:
            return {
                ...state,
                user:null
            }
        case AuthActions.Sin_Up_start:
        case AuthActions.login_start:
            return{
                ...state,
                ErrorMessage:null,
                loading:true
            }
            
        case AuthActions.AuthenticatedFAILD:
            console.log(action.payload)
            return {
                ...state,
                user:null,
                ErrorMessage:action.payload,
                loading:false
                
            }
    }       


    return state
}