import * as TasksAction from './Task.Action';
import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Actions, createEffect, Effect, ofType } from "@ngrx/effects";
import { Store } from "@ngrx/store";
import { of } from "rxjs";
import { catchError, map, switchMap, withLatestFrom } from "rxjs/operators";
import * as TasksStore from'./../../store/app.reducer'
import { TasksModule } from '../tasks-model/tasks-model.module';
@Injectable()
export class tasksEffects {


     
    
    
        @Effect({dispatch:false})
        storeTasks= this.acion$.pipe(
            ofType(TasksAction.store_Tasks),
            withLatestFrom(this.store.select('Tasks')),
            switchMap(([actionData,taskState])=>{

                return this.http.put(
                    'https://httprequestt-default-rtdb.firebaseio.com/task.json',
                    taskState.tasks
                )
            }),map(data=>{
                console.log(data)
            }),catchError(err=>{
                console.log(err);
                return of({})
            })
            
            
        )

        
    


    constructor(private http:HttpClient, private acion$:Actions,private store:Store<TasksStore.AppState>){}
}