import { Injectable } from "@angular/core";
import { Subject } from "rxjs";
import { InnerTasks, UserTaskList } from "src/app/user/userTaskList.module";


@Injectable(
    {providedIn:"root"}
    )


export class UserTaskLists{


tasks = new Subject<UserTaskList[]>();

private tasksList:UserTaskList[]=[];

addusers(user:UserTaskList){

this.tasksList.push(user);

this.tasks.next(this.tasksList.slice());

}
getusers(){
    return this.tasksList
}
getUser(i){
    return this.tasksList[i]
}
deleteUSer(i){
    this.tasksList.splice(i,1);
    this.tasks.next(this.tasksList.slice())
}
addInnerTask(i:number,task:InnerTasks){
this.tasksList[i].tasks.push(task);
this.tasks.next(this.tasksList)
}
DeleteinnerTask(taskIndex,i:number){
this.tasksList[taskIndex].tasks.splice(i,1);
this.tasks.next(this.tasksList)
}
getInnerTask(taskIndex,innerIndex){
    return this.tasksList[taskIndex].tasks[innerIndex]
}
editInnerTask(user,i,task:InnerTasks){
    this.tasksList[user].tasks[i]=task;
    this.tasks.next(this.tasksList);

    

}
}