// import {  Effect,Actions, ofType } from "@ngrx/effects";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { Actions, createEffect, Effect, ofType } from "@ngrx/effects";
import { of } from "rxjs";
import { catchError, switchMap ,map, tap} from "rxjs/operators";
import { User } from "src/app/user/user.module";
import * as fromAuthAction from './auth.actions'
export interface AuthRespose{
    kind:string;
    idToken:string;
    email:string;
    refreshToken:string;
    expiresIn:string;
    localId:string;
    // ?this is mean that it's optional
    registered?:boolean;  
}
@Injectable()

export  class AuthEffects {
    @Effect()
    authSignUp = this.action$.pipe(
        ofType(fromAuthAction.Sin_Up_start),
        switchMap((siUpdata:fromAuthAction.signUpStart)=>{
            return this.http
.post<AuthRespose>(
    `https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyCxSIovXABL9J3NBNV1PRXNtW_a_5ZEO1s`
    ,
{
    email:siUpdata.payload.email,
    password:siUpdata.payload.password,
    returnSecureToken:true
}
).pipe(
    map(data=>{
        const expressionDate =new Date( new Date().getTime() + +data.expiresIn*1000)

        return new fromAuthAction.Signup(
            {email:data.email
                ,userId:data.localId
                ,token:data.idToken,
                expirationDate:expressionDate}
            )
        

    }),catchError(errRsp=>{
  
            let message = 'unkown Error occurced';
            console.log(errRsp)
            if(!errRsp.error||!errRsp.error.error){
                return of(new fromAuthAction.LoginFaild(message));

              }
              switch(errRsp.error.error.message){
                case 'EMAIL_EXISTS':
                    message = 'this email exists already';
                    break;
                case "EMAIL_NOT_FOUND":
                    message="this email does't exist";
                    break;
                case "INVALID_PASSWORD":
                    message= 'please enter valid passowrd';
                    break;

                }

            return of( new fromAuthAction.LoginFaild(message))

    })
) 

        }),tap(()=>{
            this.router.navigate(['/tasks'])
        })
        
    )

    @Effect()
    authLogin =   this.action$.pipe(
        ofType(fromAuthAction.login_start),
    switchMap((authData:fromAuthAction.LoginStart)=>{
        return this.http.post<AuthRespose>(
            'https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyCxSIovXABL9J3NBNV1PRXNtW_a_5ZEO1s'
            ,        {
                        email:authData.payload.email,
                        password:authData.payload.password,
                        returnSecureToken:true
                    
                    }
            
        ).pipe(
            map(AuthData=>{
                const expressionDate =new Date( new Date().getTime() + +AuthData.expiresIn*1000)
     

                return new fromAuthAction.login(
                        {email:AuthData.email
                            ,userId:AuthData.localId
                            ,token:AuthData.idToken,
                            expirationDate:expressionDate}
                        )

            }),
            tap(AuthSucces=>{
                // const expressionDate =new Date( new Date().getTime() + +AuthSucces.payload.*1000)
                const user = new User(
                   AuthSucces.payload.userId,
                   AuthSucces.payload.email,
                   AuthSucces.payload.token
                   ,AuthSucces.payload.expirationDate);

                   localStorage.setItem('user',JSON.stringify( user));
                   this.router.navigate(['tasks'])
            }),
            catchError((errRsp)=>{
                let message = 'unkown Error occurced';
                console.log(errRsp)
                if(!errRsp.error||!errRsp.error.error){
                    return of(new fromAuthAction.LoginFaild(message));

                  }
                  switch(errRsp.error.error.message){
                    case 'EMAIL_EXISTS':
                        message = 'this email exists already';
                        break;
                    case "EMAIL_NOT_FOUND":
                        message="this email does't exist";
                        break;
                    case "INVALID_PASSWORD":
                        message= 'please enter valid passowrd';
                        break;

                    }

                return of( new fromAuthAction.LoginFaild(message))
            })
        )
        })
)


autoLOGIN = createEffect(()=>
    
    this.action$.pipe(
        ofType(fromAuthAction.AutoLogIn),
        map(()=>{
            const userData:{
                id:string ,
                email: string,
                _token:string ,
                  _tokenExprationDate:string,
            }
            = JSON.parse(localStorage.getItem('user'));
            if(userData){
                const loadedData =
        new User(
            userData.id,
            userData.email,
            userData._token,
            new Date(userData._tokenExprationDate));
            if(loadedData.token){
    
                return new fromAuthAction.login(
                    {
                    email:userData.email,
                    userId:userData.id,
                    token:userData._token,
                    expirationDate:new Date(userData._tokenExprationDate)
                    }
                    
                    )
            }
            return {type:'dummy'}
        }
        return {type:'dummy'}
    
    
            
        })
    )
    ) 
    @Effect({dispatch:false})
logOut = this.action$.pipe(
    ofType(fromAuthAction.LOGOUT),
    map(()=>{
        localStorage.removeItem('user');
        this.router.navigate(['/home']);
        return new fromAuthAction.LogOut();
    })
)

 
@Effect({dispatch:false})
loginSucces = this.action$.pipe(
    ofType(fromAuthAction.AuthenticatedSucces),
    tap(()=>{
        // this.router.navigate(['/tasks/0'])
    })

)

constructor(private action$:Actions, private http:HttpClient,private router:Router){}

}