import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngrx/store';
import {  Observable, Subscription } from 'rxjs';
import { TaskDialogComponent } from '../task-dialog/task-dialog.component';
import { TasksModule } from '../tasks-model/tasks-model.module';
import * as taskactions from '../store/Task.Action'
import { AppState } from 'src/app/store/app.reducer';
@Component({
  selector: 'app-tasks-list',
  templateUrl: './tasks-list.component.html',
  styleUrls: ['./tasks-list.component.scss'],
  encapsulation:ViewEncapsulation.None
})
export class TasksListComponent implements OnInit {
  // it should be observable
// tasks:TasksModule[];
tasks:Observable< { tasks: TasksModule[] }>;
subscripe:Subscription

totalProject:number
  constructor(
    private store:Store<AppState>,
    public dialog:MatDialog,


    ) { }

  ngOnInit(): void {
this.tasks = this.store.select('Tasks');
this.subscripe =this.tasks.subscribe(task=>{

  this.totalProject = task.tasks.length
})
  

// this.tasks.filter(task!='asd')


    

    

  }
  addTask(){
    this.dialog.open(TaskDialogComponent).afterOpened().subscribe(()=>{
      this.store.dispatch(new taskactions.StopEdit())

      
      
    
    })
  }
  new(){
    this.dialog.open(TaskDialogComponent)
  }
  search(i){
const list =document.getElementById('list');
Array.from(list.children)
.filter(task=>!task.textContent.includes(i.value.trim()))
.forEach(task=>task.classList.add('filterd'))
Array.from(list.children)
.filter(task=>task.textContent.includes(i.value.trim()))
.forEach(task=>task.classList.remove('filterd'))


  }
ngOnDestroy(): void {
this.subscripe.unsubscribe()
}
  
}
