import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { AuthGuard } from "../login/auth.guard";
import { TaskItemComponent } from "./tasks-list/task-item/task-item.component";
import { TaskPreviewDialogComponent } from "./tasks-list/task-item/task-preview-dialog/task-preview-dialog.component";
import { TasksStartComponent } from "./tasks-start/tasks-start.component";
import { TasksComponent } from "./tasks.component";

const routes:Routes =[

    {path:'', component:TasksComponent,
canActivate:[AuthGuard],
children:[
  {path:'taskItem', component:TaskItemComponent},
  {path:':id',component:TaskPreviewDialogComponent},

]},
{path:'taskStart/:id/:title',component:TasksStartComponent}

]

@NgModule({
    imports:[
        RouterModule.forChild(routes)
    ],
    exports:[RouterModule]
})

export class TasksRoutingModule{};