addTag(tag){
  const TagsM =new TagsModule(tag.value) ;
  var maxLength = 15;
   if(tag.value.length > maxLength){
    this.errorMessage='too many character';
    // this.charac = tag.value.length
     return false;
   }

   this.tags.push(tag.value);
 
this.store.dispatch(new taskactions.StartEditing(+this.id))
  this.store.dispatch(new taskactions.Tags(TagsM)) ;
  this.store.subscribe(tasks=>console.log(tasks))

   tag.value = '';
 
   return true;

}
